.feedy {
  position: relative;
  width: 100%; }

.feedy-grid {
  display: grid;
  margin-left: auto;
  margin-right: auto;
  grid-template-columns: 1fr 2fr;
  column-gap: 24px; }

.feedy-image {
  max-width: 100%;
