  opacity: 1;
  display: block;
  width: 100%;
  height: auto;
  transition: .5s ease;
  backface-visibility: hidden;
  margin-top: 50px;
  margin-bottom: 50px; }
  .feedy-image:hover {
    transform: scale(1.05); }

.feedy-description {
  transition: .5s ease;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  text-align: center; }

.feedy:hover .feedy-image {
  opacity: 0.3; }

.feedy:hover .feedy-description {
  opacity: 1; }

